export default {
  name: "OSignIn",
  props: ['value'],

  data() {
    return {
      showSignSuccess: false,
      iAmount: 0,
      showSign: this.value
    }
  },
  watch: {
    value(v) {
      this.showSign = v
    }
  },
  methods: {
    handleClose(){
      this.$emit('input', false)
      this.showSign = false
    },
    handlePopClose() {
      this.$emit('input', false)
      this.showSign = false
      this.showSignSuccess = false
    },
    async handleSign() {
      let res = await this.$axios.post('/v1/emall/CheckIn', {})
      this.showSign = false
      this.showSignSuccess = true
      this.iAmount = res.iAmount
      this.$store.dispatch('UPDATE_USERINFO')
    },

  },
}
